.main-footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

.main-footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0;
  flex-direction: column;
  background-color: #FFFFFF;
  align-items: center;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    margin-left: 55px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0;
  }
}

.main-footer__hashtag {
  margin: 34px;
  padding: 0;

  @media (min-width: @tablet-width) {
    padding-bottom: 45px;
  }
}

.main-footer__social {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-evenly;
    margin-left: 97px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
  }
}


/*************************************
 * tablet styles
 *************************************/

@media (min-width: @tablet-width) {
  .main-footer {
    width: @tablet-width;
    margin: 0 auto;
    background-color: @white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
  }

  .main-footer__hashtag {
    margin: 0;
    display: flex;
  }
}

/*************************************
 * desktop styles
 *************************************/
@media (min-width: @desktop-width) {
  .main-footer {
    width: @desktop-width;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }

  .main-footer__hashtag {
    margin: 0;
  }
}
